@use "styles/stylesFunctions.scss" as f;
@use "styles/sassVariables" as v;

.body {
  padding-top: 60px;
  padding-bottom: 60px;

  h1 {
    font: normal normal medium 50px/67px GE SS Two;
    color: v.$secondary;
    font-size: 45px;
    letter-spacing: 0;

    margin-bottom: 20px;

    transition: v.$transition;

    &::after {
      content: "";
      height: 2px;
      background-color: v.$secondary;

      position: absolute;
      bottom: -20px;
      right: 0;
      width: 0;
      transition: v.$transition;
    }
  }
}
